import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
export default {
	components: {uniNumberBox},
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			//订单详情
			orderDetail:{},
			numberValue: 0
		}
	},
	onLoad(option){
		//商品数据
		let createOrderParam = JSON.parse(option.orderDetail);
		this.orderDetail = createOrderParam;
		console.log("订单信息：",this.orderDetail)
	},
	methods: {
		bindChange(value) {
			this.numberValue = value
			console.log("数量：",this.numberValue)
		},
		//修改手机号
		modifyPhone(){
			uni.showModal({
				title:"修改"
			})
		},
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		submit(){
			//跳转支付页面，
			let _this = this;
			let orderParam = {
				vehicleType:1,
				vehicleNumber: '浙A32132',
				totalAmount:10,
				lng:'',
				lat:'',
				productId:1,
				address:'',
				couponId:1,
				payAmount:1
			}
			_this.$api.request(orderParam,'','POST').then(res => {
				
			})
		},
		stopPrevent(){}
	}
}
